import React from 'react';
import OnlineFriends from './OnlineFriends/OnlineFriends';
import { connect } from 'react-redux';
import Navbar from './Navbar';




let mapToStateProps = (state) => {
  
  let friendsEl = state.navbarPage.online.map(f => <OnlineFriends name={f.name} id={f.id} photo={f.photo} />);

  let OnlineEl = friendsEl;
  return {
    friendsEl: OnlineEl
  }
}
let mapToDispatchProps = (dispatch) => {
  return {

  }
}


const NavbarContainer = connect(mapToStateProps, mapToDispatchProps)(Navbar)


export default NavbarContainer;